.navigation--primary {
    ul {
        list-style: none;
        margin-left: 0;
        margin-top: 0;
        max-width: unset;
    }

    .navigation__level1 {
        @include h5style();

        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        list-style: none;
        margin-top: 0;

        @include media('>=desktop') {
            justify-content: flex-end;
        }

        > li {
            margin-right: $measure--small2;

            > a {
                min-height: $minimum-control-height;
            }

            &:last-of-type {
                margin-right: 0;
            }
        }
    }

    /*
     * Primary navigation curation.
     */
    .navigation--primary__level1 {
        &[data-title='Prospective students'],
        &[data-title='About us'] {
            .navigation__level2 {
                grid-template-rows: repeat(9, auto);

                > li:first-of-type {
                    grid-row: 1 / span 9;
                    justify-content: flex-start;
                }
            }
        }

        &[data-title='Research'],
        &[data-title='For businesses'],
        &[data-title='Alumni and supporters'] {
            .navigation__level2 {
                grid-template-rows: repeat(4, auto);

                > li:first-of-type {
                    grid-row: 1 / span 4;
                    justify-content: flex-start;
                }
            }
        }

        &[data-title='News and events'] {
            .navigation__level2 {
                grid-template-rows: repeat(2, auto);

                > li:first-of-type {
                    grid-row: 1 / 1;
                    justify-content: flex-start;
                }
            }
        }
    }

    .navigation__level2 {
        column-gap: $measure--small2;
        display: grid;
        grid-auto-flow: column;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(4, auto);
        row-gap: $measure--half;

        > li {
            @include h5style();

            margin-top: 0;

            &:not(:first-of-type) {
                a {
                    align-items: center;
                    display: flex;
                    height: 100%;
                }
            }

            > a,
            > .navigation__current > span {
                &:after {
                    @include font-family('icon');

                    content: '\f054';
                    font-size: $font-size--icon;
                    font-weight: $font-weight-light;
                    margin-left: $measure--half;
                }
            }
        }

        a {
            @include navigation-anchor(
                $color: $color-purewhite,
                $background: $color-gray10,
                $hover-color: $color-purewhite,
                $hover-background: $color-gray8,
                $outline: $color-purewhite
            );
        }

        a,
        .navigation__current > span {
            display: block;
            padding-bottom: $measure--half;
            padding-left: $measure--half;
            padding-right: $measure--half;
            padding-top: $measure--half;
            width: 100%;

            & > span {
                display: inline;
            }
        }

        .navigation__hierarchy > a > span {
            border-bottom-color: $color-purewhite;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
        }

        .navigation__current > a {
            align-items: center;

            > span {
                border-bottom-color: $color-red50;
                border-bottom-style: $border-style-solid;
                border-bottom-width: $border-width-default;
            }
        }
    }

    .navigation__level2 > li:first-of-type {
        grid-row: 1 / span 4;

        > .navigation__level3 {
            display: block;
        }
    }

    .navigation__level3 > li {
        @include content-style();
        @include smalltype();

        margin-top: 0;
    }

    .navigation__level3 {
        display: none;

        a,
        .navigation__current > span {
            padding-left: $measure--half;
        }
    }

    .navigation__level1,
    .navigation__level2,
    .navigation__level3 {
        max-width: unset;
    }

    .navigation--primary__menu {
        background-color: $color-gray10;
        color: $color-purewhite;
        left: 0;
        min-height: 20vh;
        position: absolute;
        width: 100%;
        z-index: z-index(navigation);
    }

    .navigation--primary__menu__content {
        @include standard-box();

        column-gap: $measure--small2;
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: repeat(2, auto);
        padding-bottom: $measure--basic;
        padding-left: $measure--basic;
        padding-right: $measure--basic;
        padding-top: $measure--basic;

        .navigation__current {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    .wrapper--navigation--primary__menu__close {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        grid-column-end: span 4;
        grid-column-start: 1;
        grid-row-end: 1;
        grid-row-start: 1;
        justify-content: flex-end;

        button {
            @include navigation-button(
                $color-purewhite,
                $color-gray10,
                $color-gray8,
                $color-purewhite
            );
            @include h5style();

            margin-top: 0;
            padding: $measure--quarter;
        }
    }

    .navigation--primary__menu__close__text {
        @include sr-only();
    }

    .navigation--primary__menu__header {
        @include h4style();

        margin-top: 0;

        a,
        > span {
            display: block;
            padding-bottom: $measure--half;
            padding-left: $measure--half;
            padding-right: $measure--half;
            padding-top: $measure--half;

            > span {
                display: inline;
            }
        }

        a {
            @include navigation-anchor(
                $color: $color-purewhite,
                $background: $color-gray10,
                $hover-color: $color-purewhite,
                $hover-background: $color-gray8,
                $outline: $color-purewhite
            );
        }

        a,
        > span {
            &:after {
                @include font-family('icon');

                content: '\f054';
                font-size: $font-size--icon;
                font-weight: $font-weight-light;
                margin-left: $measure--half;
            }
        }
    }

    .navigation__hierarchy {
        .navigation--primary__menu__header {
            span {
                border-bottom-color: $color-purewhite;
                border-bottom-style: $border-style-solid;
                border-bottom-width: $border-width-default;
            }
        }
    }

    .navigation__current {
        .navigation--primary__menu__header {
            span {
                border-bottom-color: $color-red50;
                border-bottom-style: $border-style-solid;
                border-bottom-width: $border-width-default;
            }
        }
    }

    .navigation--primary__menu {
        display: none;
    }

    .navigation--primary__level1 {
        & > button div,
        & > a {
            border-bottom-width: $border-width-default;
            border-bottom-style: $border-style-solid;
            border-bottom-color: transparent;
        }

        &.navigation__hierarchy {
            & > button div,
            & > a {
                border-bottom-color: $color-nearblack;
            }
        }

        &.navigation__current {
            & > button div,
            & > a {
                border-bottom-color: $color-red50;
            }
        }
    }
}

@mixin open-menu-properties() {
    & > div:after {
        color: $color-gray10;
    }

    & + .navigation--primary__menu {
        box-shadow: 0 $measure--half $measure--small1 0 rgba(0, 0, 0, 0.3);
        display: block;
        margin-top: $measure--small2;
        padding-bottom: $measure--small2;
    }
}

.js {
    .navigation--primary {
        .navigation__level1 {
            > li {
                .navigation__button {
                    @include anchor-reset();
                    @include anchor-default-outline($color-nearblack);

                    position: relative;

                    &:hover {
                        @include media('hover') {
                            div {
                                border-bottom-color: $color-red50;
                            }
                        }
                    }

                    & > div:after {
                        @include font-family('icon');

                        color: transparent;
                        content: '\f0d8';
                        display: block;
                        font-size: $measure--small4;
                        font-weight: $font-weight-black;
                        left: 0;
                        position: absolute;
                        top: $measure--small2;
                        width: 100%;
                    }

                    &[aria-expanded='true'] {
                        @include open-menu-properties();
                    }
                }
            }
        }
    }

    .navigation__veil {
        @include veil();

        &[data-on='true'] {
            @include media('<small') {
                display: none;
            }
        }
    }

    .navigation__button__navigation,
    .navigation__button__open-close {
        @include sr-only();
    }
}

.no-js .navigation--primary {
    .navigation__level1 {
        > li {
            &:hover,
            &:focus-within {
                & > .navigation--primary__menu {
                    box-shadow: 0 $measure--half $measure--small1 0
                        rgba(0, 0, 0, 0.3);
                    display: grid;
                }
            }
        }
    }
}

.navigation--primary {
    .navigation--primary__level1 .navigation__button:first-child {
        display: flex;

        &:after {
            content: '\f078';
            font-family: 'Font Awesome';
            display: inline-flex;
            color: $color-red50;
            @include extrasmalltype();
            margin-left: $measure--half;
            align-items: flex-end;
        }

        & + .navigation--primary__menu {
            transition: opacity 0.7s;
            visibility: hidden;
            display: block;
            opacity: 0;
        }

        &[aria-expanded='true'] + .navigation--primary__menu {
            visibility: visible;
            opacity: 1;
        }
    }
}

// Temp burger bun styling below

.menu__display__button__button {
    flex-direction: column !important;
}

.menu {
    .burger-bun {
        display: block;
        position: relative;
        z-index: 1;
        -webkit-user-select: none;
        user-select: none;

        input {
            display: block;
            width: 40px;
            height: 32px;
            position: absolute;
            top: -7px;
            left: -5px;
            cursor: pointer;
            opacity: 0; /* hide this */
            z-index: 2; /* and place it over the hamburger */
            -webkit-touch-callout: none;

            &:checked ~ span {
                opacity: 1;
                transform: rotate(45deg) translate(-2px, -1px);
                background: white;
            }

            &:checked ~ span:nth-last-child(2) {
                opacity: 0;
                transform: rotate(0deg) scale(0.2, 0.2);
            }

            &:checked ~ span:nth-last-child(1) {
                transform: rotate(-45deg) translate(0, -1px);
            }
        }

        span {
            display: block;
            width: 33px;
            height: 4px;
            margin-bottom: 5px;
            position: relative;
            background: white;
            border-radius: 3px;
            z-index: 1;

            transform-origin: 4px 0px;

            transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
                background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
                opacity 0.55s ease;

            &:first-child {
                transform-origin: 0% 0%;
            }

            &:nth-last-child(1) {
                transform-origin: 0% 100%;
            }
        }
    }
}

// mobile menu transition

#global-header__menu {
    //display: block;

    .menu__content {
        opacity: 0;
        transition: opacity 0.5s;
        visibility: hidden;
        flex-direction: column;
        display: flex;
        flex-wrap: nowrap;
        left: 0;
        position: absolute;
        right: 0;
        overflow: hidden;
    }

    &[data-open='true'] {
        .menu__content {
            visibility: visible;
            opacity: 1;
        }
    }
}
